*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@media screen and (prefers-reduced-motion: reduce) {
  *,
*::before,
*::after {
    animation-duration: 0.001ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.001ms !important;
  }
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

img,
picture,
svg {
  display: block;
}

button {
  font: inherit;
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

html,
body {
  overflow-x: hidden;
}

.container {
  width: min(68.5rem, 100% - 2rem);
  margin-inline: auto;
  display: flex;
}

.container--wide {
  width: min(82.5rem, 100% - 2rem);
  margin-inline: auto;
  display: flex;
}

a {
  text-decoration: none;
  color: #fff;
}

.active {
  user-select: none;
}

html {
  font-family: "Roboto", sans-serif;
}

h1,
h2,
h3 {
  font-weight: 700;
}

p {
  font-weight: 300;
}

h1 {
  font-size: 2.375rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.375rem;
}

.slider {
  position: relative;
  user-select: none;
}
.slider__image {
  display: none;
  background-color: #101010;
}
.slider__image img {
  animation-name: fade;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  width: 100%;
}
.slider__controls {
  position: absolute;
  bottom: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  text-align: center;
  width: 100%;
}
@media only screen and (max-width: 60em) {
  .slider__controls {
    bottom: 9rem;
  }
}
@media only screen and (max-width: 40em) {
  .slider__controls {
    bottom: 14rem;
    gap: 1rem;
  }
}
.slider__arrow {
  cursor: pointer;
  font-size: 2rem;
  color: #fff;
  transition: all 0.4s;
}
.slider__arrow:hover {
  transform: scale(1.2);
}
.slider__arrow:active {
  transform: scale(1);
  transition: 0.1s;
}
@media only screen and (max-width: 40em) {
  .slider__arrow {
    font-size: 1.5rem;
  }
}
.slider__dot {
  cursor: pointer;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: #fff;
  border: 2px solid #fff;
  outline: 1px solid black;
  transition: all 0.4s;
}
.slider__dot:hover {
  background-color: #a62120;
}
@media only screen and (max-width: 40em) {
  .slider__dot {
    width: 0.75rem;
    height: 0.75rem;
  }
}
.slider .active {
  background-color: #a62120;
}

@keyframes fade {
  0% {
    filter: opacity(0.5);
  }
  100% {
    filter: none;
  }
}
.button {
  background-color: #fff;
  color: #252525;
  font-size: 1rem;
  border-radius: 2.5rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 10.5rem;
  height: 3rem;
  border: 1px solid #ff0000;
  transition: all 0.4s;
}
.button:hover {
  background-color: transparent;
  transform: scale(1.05);
  border-color: #fff;
  outline: 1px solid #fff;
  color: #fff;
  font-weight: 600;
}
.button:active {
  transform: scale(1);
  transition: none;
}

.button--accent {
  background-color: #a62120;
  color: #fff;
  border: 1px solid #fff;
  transition: all 0.4s;
}
.button--accent:hover {
  background-color: transparent;
  transform: scale(1.05);
  border-color: #fff;
  outline: 1px solid #fff;
  font-weight: 600;
}
.button--accent:active {
  transform: scale(1);
  transition: none;
}

.separator {
  position: relative;
  overflow: visible;
  border: none;
  background-color: #535353;
  height: 1px;
  margin-block: 3rem;
  z-index: 1;
}
.separator::after {
  content: "";
  position: absolute;
  display: block;
  width: 4.5rem;
  height: 4.5rem;
  background-image: url(/img/images/separator.png);
  background-size: contain;
  top: -2.25rem;
  left: 50%;
  margin-left: -2.25rem;
  z-index: 999;
}
.separator::before {
  content: "";
  position: absolute;
  display: block;
  width: 5.5rem;
  height: 5.5rem;
  top: -2.75rem;
  left: 50%;
  margin-left: -2.75rem;
  background-color: #000000;
  z-index: 99;
}
@media only screen and (min-width: 60em) {
  .separator::before {
    background-color: #101010;
  }
}

.header {
  display: flex;
  flex-direction: column;
  background-color: #b50014;
  user-select: none;
}
.header .header__nav {
  width: 100%;
  max-height: 6rem;
  display: flex;
  gap: 2.5rem;
  justify-content: space-between;
  font-weight: 700;
  position: relative;
}
@media only screen and (max-width: 75em) {
  .header .header__nav {
    gap: 1.2rem;
  }
}
@media only screen and (max-width: 60em) {
  .header .header__nav {
    gap: 1rem;
    padding-block: 1.25rem;
    justify-content: flex-end;
  }
}
.header .header__nav .logo {
  max-width: 11rem;
  z-index: 999;
  position: absolute;
}
@media only screen and (max-width: 70em) {
  .header .header__nav .logo {
    left: -1rem;
    top: 0;
    max-width: 9rem;
  }
}
@media only screen and (max-width: 40em) {
  .header .header__nav .logo {
    max-width: 6rem;
  }
}
.header .header__nav .navigation {
  display: flex;
  justify-content: flex-end;
  flex: 1;
  align-items: center;
  gap: 2.5rem;
  padding-block: 2rem;
}
@media only screen and (max-width: 75em) {
  .header .header__nav .navigation {
    gap: 1.2rem;
  }
}
@media only screen and (max-width: 70em) {
  .header .header__nav .navigation {
    font-size: 0.875rem;
  }
}
@media only screen and (max-width: 60em) {
  .header .header__nav .navigation {
    gap: 1.5rem;
    flex-direction: column;
    justify-content: flex-start;
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    right: -1rem;
    padding: 0;
    z-index: 999;
    background-color: rgba(179, 0, 21, 0.8509803922);
    transform: translateX(100%);
    transition: all 0.4s ease-out;
  }
  .header .header__nav .navigation .nav-link:nth-child(1) {
    margin-top: 5rem;
  }
  .header .header__nav .navigation.mobile {
    transform: translateX(0);
    transition: all 0.4s ease-in;
  }
}
.header .header__nav a {
  transition: all 0.4s;
}
.header .header__nav a:hover {
  color: #101010;
}
.header .header__nav a:hover path {
  fill: #101010;
}
.header .header__nav path {
  transition: all 0.4s;
}
.header .header__nav .download {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  z-index: 999;
}
@media only screen and (max-width: 70em) {
  .header .header__nav .download {
    font-size: 0.875rem;
  }
}
.header .header__nav .download p {
  font-weight: 700;
}
.header .header__nav .hamburger {
  display: none;
  appearance: none;
  border: none;
  background: none;
  cursor: pointer;
  z-index: 999;
}
.header .header__nav .hamburger:hover span {
  background-color: #101010;
}
.header .header__nav .hamburger:hover span:nth-child(2) {
  transform: translateX(0.35rem);
}
.header .header__nav .hamburger.active span:nth-child(1) {
  transform: translate(0.1rem, 0.78rem) rotate(135deg);
}
.header .header__nav .hamburger.active span:nth-child(2) {
  transform: translateX(1.35rem);
  opacity: 0;
}
.header .header__nav .hamburger.active span:nth-child(3) {
  transform: translate(0.1rem, -0.78rem) rotate(-135deg);
}
.header .header__nav .hamburger.active:hover span {
  background-color: #101010;
}
.header .header__nav .hamburger span {
  display: block;
  width: 1.75rem;
  height: 0.2rem;
  background-color: #fff;
  border: 0;
  outline: 0;
  border-radius: 0.5rem;
  transform-origin: 50% 50%;
  transition: all 0.4s;
}
@media only screen and (max-width: 60em) {
  .header .header__nav .hamburger {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 0.5rem;
    padding-inline: 0.5rem;
  }
}
.header .working-hours {
  background-color: #252525;
  color: #fff;
  display: flex;
}
.header .working-hours .hours {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 0.875rem;
  padding-block: 0.4rem;
  gap: 0.4rem;
}
@media only screen and (max-width: 40em) {
  .header .working-hours .hours {
    font-size: 0.625rem;
    text-align: right;
    padding-block: 0.2rem;
  }
  .header .working-hours .hours img {
    display: none;
  }
  .header .working-hours .hours strong::after {
    content: "\a";
    white-space: pre;
  }
}

